'use client';

import { useState } from 'react';
import Image from 'next/image';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { verifyDocumentAction } from '@/app/actions';
import type { VerifyLandDocAuthenticityOutput } from '@/ai/flows/verify-land-doc-authenticity';
import { Loader2, CheckCircle, XCircle, Upload, Volume2 } from 'lucide-react';
import { Progress } from './ui/progress';
import { useSpeech } from '@/hooks/use-speech';
import { useLanguage } from '@/contexts/language-context';

export default function DocVerificationForm() {
  const [file, setFile] = useState<File | null>(null);
  const [preview, setPreview] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<VerifyLandDocAuthenticityOutput | null>(null);
  const { toast } = useToast();
  const { speak, isSpeaking } = useSpeech();
  const { language } = useLanguage();

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile) {
      setFile(selectedFile);
      setResult(null);

      const reader = new FileReader();
      reader.onloadend = () => {
        setPreview(reader.result as string);
      };
      reader.readAsDataURL(selectedFile);
    }
  };

  const handleSpeak = (text: string) => {
    speak({ text, lang: language });
  };


  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!file) {
      toast({
        title: 'No file selected',
        description: 'Please select a document image to verify.',
        variant: 'destructive',
      });
      return;
    }

    setIsLoading(true);
    setResult(null);

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = async () => {
      const photoDataUri = reader.result as string;
      try {
        const verificationResult = await verifyDocumentAction({ photoDataUri });
        setResult(verificationResult);
        const resultText = `Verification Result: ${verificationResult.isAuthentic ? 'Authentic' : 'Not Authentic'}. Confidence Score: ${(verificationResult.confidenceScore * 100).toFixed(0)}%. AI's Reasoning: ${verificationResult.reason}`;
        handleSpeak(resultText);
      } catch (error) {
        console.error('Verification failed:', error);
        toast({
          title: 'Verification Failed',
          description: 'An error occurred during document verification.',
          variant: 'destructive',
        });
      } finally {
        setIsLoading(false);
      }
    };
  };

  const getConfidenceColor = (score: number) => {
    if (score < 0.5) return 'text-destructive';
    if (score < 0.8) return 'text-yellow-500';
    return 'text-green-500';
  }

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <form onSubmit={handleSubmit}>
        <CardHeader>
          <div className="flex justify-between items-center">
            <div>
                <CardTitle>Land Document Verification</CardTitle>
                <CardDescription>
                    Upload a photo of your land ownership document. Our AI will analyze it for authenticity.
                </CardDescription>
            </div>
            <Button variant="outline" size="icon" onClick={() => handleSpeak("Land Document Verification. Upload a photo of your land ownership document. Our AI will analyze it for authenticity.")} disabled={isSpeaking}>
                <Volume2 className="h-5 w-5" />
                <span className="sr-only">Read page content</span>
            </Button>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="document-upload">Document Photo</Label>
            <div className="flex items-center gap-4">
              <Input id="document-upload" type="file" accept="image/*" onChange={handleFileChange} className="flex-grow" />
            </div>
          </div>

          {preview && (
            <div className="relative w-full h-64 border-2 border-dashed rounded-lg flex items-center justify-center">
              <Image src={preview} alt="Document preview" fill style={{ objectFit: 'contain' }} className="rounded-lg p-2" />
            </div>
          )}
        </CardContent>
        <CardFooter className="flex-col items-start gap-4">
          <Button type="submit" disabled={isLoading || !file} className="w-full sm:w-auto">
            {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Upload className="mr-2 h-4 w-4" />}
            Verify Document
          </Button>
          
          {isLoading && <p className="text-sm text-muted-foreground">AI is analyzing your document, please wait...</p>}

          {result && (
            <Card className="w-full bg-secondary">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  {result.isAuthentic ? <CheckCircle className="text-green-500" /> : <XCircle className="text-destructive" />}
                  Verification Result: {result.isAuthentic ? 'Authentic' : 'Not Authentic'}
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label>Confidence Score</Label>
                  <div className="flex items-center gap-2">
                    <Progress value={result.confidenceScore * 100} className="w-full"/>
                    <span className={cn('font-bold', getConfidenceColor(result.confidenceScore))}>
                      {(result.confidenceScore * 100).toFixed(0)}%
                    </span>
                  </div>
                </div>
                <div>
                  <Label>AI's Reasoning</Label>
                  <p className="text-sm text-muted-foreground p-3 bg-background rounded-md border">{result.reason}</p>
                </div>
              </CardContent>
            </Card>
          )}
        </CardFooter>
      </form>
    </Card>
  );
}
